@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;800&display=swap);
@-webkit-keyframes wave-hand{from{transform:rotate(0deg)}33%{transform:rotate(-20deg)}66%{transform:rotate(20deg)}to{transform:rotate(0deg)}}@keyframes wave-hand{from{transform:rotate(0deg)}33%{transform:rotate(-20deg)}66%{transform:rotate(20deg)}to{transform:rotate(0deg)}}.bio{color:#000000}@media (min-width: 1024px){.bio{max-width:40%}}.bio__name{font-size:46px;line-height:70px;color:#000000;margin-bottom:24px}@media (min-width: 767px){.bio__name{font-size:72px;line-height:90px}}.bio__name--heavy{font-weight:bold;text-transform:uppercase}.bio__waving-hand{display:inline-block;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-name:wave-hand;animation-name:wave-hand}.bio__teaser{display:none}@media (min-width: 1024px){.bio__teaser{display:block;font-size:18px;margin:48px 0}}.bio__spotahome{width:34px;vertical-align:middle}.bio__occupation{color:#b0b0b0;text-align:left;font-size:18px;line-height:24px;margin-bottom:48px}@media (min-width: 767px){.bio__occupation{font-size:24px;line-height:36px}}@media (min-width: 1024px){.bio__occupation{margin-bottom:0}}.bio__information-icon{width:30px;vertical-align:middle;margin-right:4px}.bio__information-socials{font-size:18px}@media (min-width: 767px){.bio__information-socials{font-size:18px}}.bio__information-socials *+*{margin-left:48px}.bio__information-socials-link{color:black;text-decoration:none}

.terminal{display:none}@media (min-width: 1024px){.terminal{display:flex;flex-direction:column;width:550px;height:350px;background-color:black;border-radius:8px;box-shadow:0px 9px 13px 1px #00000054;margin-top:32px}.terminal__navbar{width:100%;height:32px;border-top-left-radius:8px;border-top-right-radius:8px}.terminal__navbar-close{display:block;width:14px;height:14px;margin-left:auto;margin-right:12px;margin-top:12px;border-radius:50%;background-color:#ffc83d}.terminal__screen{font-size:16px;font-family:monospace;color:lime;overflow-y:hidden;display:flex;flex-direction:column-reverse;width:100%;height:90%}.terminal__screen-row{padding:8px 16px;margin-bottom:8px}.terminal__actions{padding:6px 10px;display:flex;flex-direction:row;flex-wrap:wrap;align-items:center}.terminal__actions-symbol{font-size:16px;font-family:monospace;color:lime}.terminal__actions-command{flex-grow:1;font-size:16px;font-family:monospace;padding:12px;outline:none;background:black;color:lime;border:none}.terminal__cursor{width:8px;height:16px;background-color:lime}}

.homepage{display:flex;align-items:center;justify-content:space-evenly;width:100vw;height:100vh;background-color:#f2f2f2;padding:24px;box-sizing:border-box}

body {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p, h1, h2, h3, h4, ul, li {
  -webkit-margin-before: 0em;
